
// 
const getClientUser = (callback) => {
	let provider = '';
	let code = '';
	uni.getProvider({service: 'oauth'}).then((result) => { 
		result = result[1];
		provider = result.provider[0];
		return uni.login({provider: provider});
	}).then((result) => { 
		result = result[1];
		code = result.code;
		return uni.getUserInfo({provider: provider});
	}).then((result) => { 
		result = result[1];
		let userInfo = result.userInfo;
		userInfo.provider = provider;
		userInfo.code = code;
		userInfo.signature = result.signature;
		callback &&callback(userInfo);
	}).catch(err => {
		uni.showModal({ title: '', content: '', showCancel: false });
	});
}

// 
const turnPage = (type, obj) => {
	let url = '';
	switch (type) {
		case 'home':
			url = '/pages/tabbar/tabbar-1/main';
			break;
		case 'my':
			url = '/pages/untabbar/my/main';
			break;
		case 'car':
			url = '/pages/untabbar/car/main';
			break;
		case 'detail':
			url = '/pages/untabbar/detail/main?id=' + obj.id;
			break;
		default:
			url = '/pages/tabbar/tabbar-1/main';
			break;
	}
	uni.navigateTo({url: url});
}

// 
const countRealPrice = (good) => {
		return (good.price * good.rebate / 10).toFixed(2);
}

export {getClientUser, turnPage, countRealPrice};